export default {
  props: {
    show: {
      type: Boolean,
      required: true
    },
    transparent: {
      type: Boolean,
      default: false
    },
    opacity: {
      type: Number,
      default: 1
    }
  },
  methods: {
      close(){
          this.$emit('close');
      }
  }
}
