export default {
    head: {
        title : '메인페이지'
    },
    data(){
        return {
            isModal: false
        }
    },
    methods: {
        timerModal(boolean){
            if(!boolean){
                return this.isModal = false;
            }
            this.isModal = true;
        }
    }
}
